/* eslint-disable react-hooks/exhaustive-deps */
import React, { Fragment, useEffect } from 'react'
import { ReviewRow } from './ReviewRow'
import { Link } from "react-router-dom"
import { IReview } from '../../../common/review';
import { ESize, useImage } from '../../hooks';

interface IUserReviewRow{
    review: IReview;
    onDelete: (reviewId: number) => void;
}

export const UserReviewRow: React.FC<IUserReviewRow> = ({review, onDelete}: IUserReviewRow)=> {
    const [imgsrc, onImgError, setImgsrc] = useImage("/rewer/uploads/posters", `0.jpg`, ESize.Small);
    useEffect(() => {
        if(review.seriesId){
            setImgsrc("/rewer/uploads/seriesposters", `${review.seriesId}.jpg`);
        } else {
            setImgsrc("/rewer/uploads/posters", `${review.movieId}.jpg`);
        }
    }, [review]);
    return (
        <div className="followed-review-grid">
            <div className="review-poster-container">          
                    {(review.seriesId != null)?
                    (<Fragment><Link  to={`/rewer/series?seriesId=${review.seriesId}`} >
                        <img src={imgsrc} alt={`${review.movieTitle}-poster`} onError={onImgError} className="review-poster" />
                        <p>{review.seriesTitle}</p>
                        </Link>
                        <Link to={`/rewer/movie?movieId=${review.movieId}`}><p>{review.movieTitle}</p></Link>
                    </Fragment>
                    ):
                    (<Link to={`/rewer/movie?movieId=${review.movieId}`} >
                        <img src={imgsrc} alt={`${review.movieTitle}-poster`} onError={onImgError} />
                        <p>{review.movieTitle}</p>
                    </Link>)}
            </div>
            <div>
                <ReviewRow review={review} onDelete={onDelete} />
            </div>
        </div>
    )
}

export default UserReviewRow;