import React, { useState, useEffect, useRef } from 'react'
import { IMovie } from '../../../common/movie';
import { ESize, useImage } from '../../hooks';

interface IMovieDiv{
    movie: IMovie;
}

export const MovieDiv: React.FC<IMovieDiv> = ({movie}: IMovieDiv) => {
    const [imgsrc, onImgError] = useImage("/rewer/uploads/posters", `${movie.id}.jpg`, ESize.Medium, true);
    const [imgClass, setImgClass] = useState("");
    const [scoreClass, setScoreClass] = useState("");
    const scoreLine = useRef<HTMLDivElement>();
    useEffect(()=>{
        if(movie.avgScore > 6){
            setImgClass("high-poster");
            setScoreClass("high");
            scoreLine.current.style.display = "block";
        } else if(movie.avgScore > 3){
            setImgClass("medium-poster");
            setScoreClass("medium");
            scoreLine.current.style.display = "block";
        } else if(movie.avgScore > 0){
            setImgClass("low-poster");
            setScoreClass("low");
            scoreLine.current.style.display = "block";
        } else {
            scoreLine.current.style.display = "none";
        }
    }, [movie]);

    useEffect(()=>{

    }, [movie])

    return (
        <div className="movie-container fade">
            <img className={imgClass} src={imgsrc} alt={`${movie.title}-poster`} onError={onImgError} />
            <p className="movie-title">{movie.title}</p>
            <div ref={scoreLine} className={scoreClass} >
                <p className="score-line">{ Math.round(movie.avgScore * 10)/10}</p>
            </div>
        </div>
    )
}

export default MovieDiv;